import { authAPI } from './../api/api'

const SET_ERROR = 'auth/SET_ERROR'
const SET_AUTHED_USER = 'auth/SET_AUTHED_USER'

let initialState = {
    id: null,
    email: null,
    login: null,
    isAuth: false,
    error: null,
}

const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_AUTHED_USER:
            return {
                ...state,
                ...action.payload,
            }
        case SET_ERROR:
            return {
                ...state,
                error: action.error,
            }
        default:
            return state
    }
}
const setAuthedUser = (id, email, login, isAuth) => ({
    type: SET_AUTHED_USER,
    payload: {
        id,
        email,
        login,
        isAuth,
    },
})
const setError = (error) => ({
    type: SET_ERROR,
    error,
})

export const getAuthedUser = () => {
    return async (dispatch) => {
        let userInfo = await authAPI.authUser()
        if (userInfo.resultCode === 0){
            let {id, email, login} = userInfo.data
            dispatch(setAuthedUser(id, email, login, true))
        }
    }
}
export const loginUser = (userData) => {
    return async (dispatch) => {
        let response = await authAPI.login(userData)
        if (response.data.resultCode === 0){
            dispatch(getAuthedUser())
            dispatch(setError(null))
        }
        if (response.data.resultCode !== 0){
            dispatch(setError(...response.data.messages))
        }
    }
}
export const logoutUser = () => {
    return async (dispatch) => {
        let response = await authAPI.logout()
        if (response.data.resultCode === 0){
            dispatch(setAuthedUser(null, null, null, false))
        }
    }
}

export default authReducer
