import classes from './Paginator.module.css'
import * as React from 'react'
import { useEffect, useState } from 'react'

const Paginator = ({portionSize = 10, ...props}) => {
    let pagesCount = Math.ceil(props.totalItemsCount / props.pageSize)
    let pages = []
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i)
    }

    let portionCount = Math.ceil(pagesCount / portionSize)
    let [portionNumber, setPortionNumber] = useState(1)
    let leftPortionPageNumber = (portionNumber - 1) * portionSize + 1
    let rightPortionPageNumber = portionNumber * portionSize
    useEffect(() => {
        setPortionNumber(Math.ceil(props.currentPage / portionSize))
    }, [props.currentPage])

    return (
        <div>
            { portionNumber > 1 &&
            <button className={ `${classes.pageButton} ${classes.nextPrevButton}` }
                    onClick={ () => setPortionNumber(portionNumber - 1) }>
                <code>&lt;&lt;</code>
            </button> }

            { pages
                .filter(p => p >= leftPortionPageNumber && p <= rightPortionPageNumber)
                .map((p) => {
                    return <button className={ props.currentPage === p
                        ? classes.selectedPage
                        : classes.pageButton }
                                   key={ p }
                                   onClick={ () => {
                                       props.onPageChanged(p)
                                   } }>{ p + ' ' }</button>

                }) }
            { ((portionNumber > 2) && (portionCount > portionNumber))
                ? <button className={ classes.pageButton }
                          onClick={ () => { props.onPageChanged(pagesCount) } }> ...{ pagesCount } </button>
                : null
            }
            { portionCount > portionNumber &&
            <button className={ `${classes.pageButton} ${classes.nextPrevButton}` }
                    onClick={ () => setPortionNumber(portionNumber + 1) }> <code>&gt;&gt;</code> </button>
            }
        </div>
    )
}

export default Paginator