import { composeValidators, maxLength, requiredField } from '../../../utils/valiators/validators'
import { Field, Form } from 'react-final-form'
import classes from '../../Login/Login.module.css'
import React from 'react'
import { useSelector } from 'react-redux'
import { getFromAuth } from '../../../Redux/selectors/profile-selectors'

let maxLength30 = maxLength(30)
let maxLength20 = maxLength(20)

export const LoginForm = (props) => {
    const error = useSelector(state => getFromAuth.authError(state))
    return (
        <Form
            onSubmit={ props.onSubmit }
            initialValues={ {rememberMe: true} }
            render={ ({handleSubmit, submitting, pristine}) => (
                <form onSubmit={ handleSubmit }>
                    <Field name={ 'email' }
                           validate={ composeValidators(requiredField, maxLength30) }>
                        { ({input, meta}) => (
                            <div>
                                <div className={ classes.input }>
                                    <input { ...input } type={ 'email' } placeholder={ 'E-mail:' }/>
                                </div>
                                <div className={ classes.error }>
                                    { meta.error && meta.touched && <span>{ meta.error }</span> }
                                </div>
                            </div>
                        ) }
                    </Field>
                    <Field name={ 'password' }
                           validate={ composeValidators(requiredField, maxLength20) }>
                        { ({input, meta}) => (
                            <div className={ classes.input }>
                                <div>
                                    <input { ...input } type={ 'password' }
                                           placeholder={ 'Пароль:' }/>
                                </div>
                                <div className={ classes.error }>
                                    { (meta.error && meta.touched &&
                                        <span>{ meta.error }</span>) ?? <span>{ error }</span> }
                                </div>
                            </div>
                        ) }
                    </Field>
                    <Field
                        name={ 'rememberMe' }
                        component={ 'input' }
                        type={ 'checkbox' }/> Запомнить меня
                    <div className={ classes.buttonWrapper }>
                        <button
                            className={ classes.button }
                            type={ 'submit' }
                            disabled={ submitting || pristine }>Войти
                        </button>
                    </div>
                </form>
            ) }
        />
    )
}
