export const getFromProfile = {
    profilePage(state) {
        return state.profilePage
    },
    status(state) {
        return state.profilePage.status
    },
    postsData(state) {
        return state.profilePage.postsData
    }
}

export const getFromAuth = {
    authUserId(state) {
        return state.authPage.id
    },
    isAuth(state) {
        return state.authPage.isAuth
    },
    authPage(state) {
        return state.authPage
    },
    authError(state) {
        return state.authPage.error
    },
    login(state) {
        return state.authPage.login
    }
}