import { Link } from 'react-router-dom'
import classes from './Sidebar.module.css'


const Sidebar = () => {
    return (
        <nav className={ classes.nav }>
            <div className={ classes.item }>
                <Link to="/profile" className={ classes.item }>
                    Моя страница
                </Link>
            </div>
            <div className={ classes.item }>
                <Link to="/dialogs"
                      className={ classes.item }>
                    Сообщения
                </Link>
            </div>
            <div className={ classes.item }>
                <Link to="/news" className={ classes.item }>
                    Новости
                </Link>
            </div>
            <div className={ classes.item }>
                <Link to="/music" className={ classes.item }>
                    Музыка
                </Link>
            </div>
            <div className={ classes.item }>
                <Link to="/settings" className={ classes.item }>
                    Настройки
                </Link>
            </div>
            <div className={ classes.item }>
                <Link to="/users" className={ classes.item }>
                    Пользователи
                </Link>
            </div>
        </nav>
    )
}

export default Sidebar