import { profileAPI } from '../api/api'

const ADD_POST = 'profile/ADD-POST'
const SET_USER_PROFILE = 'profile/SET_USER_PROFILE'
const SET_USER_STATUS = 'profile/SET_USER_STATUS'
const DELETE_POST = 'profile/DELETE_POST'
const SAVE_PHOTO_SUCCESS = 'profile/SAVE_PHOTO_SUCCESS'

let initialState = {
    postsData: [
        {id: 1, post: 'Не бросай!!', likesCount: 1000},
        {id: 2, post: 'Продолжай заниматься!', likesCount: 1001},
        {id: 3, post: 'С утра все получится', likesCount: 700},
        {id: 4, post: 'Или попозже :-)', likesCount: 379},

    ],
    profile: null,
    status: ''
}

const profileReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_POST:
            let newPost = {
                id: state.postsData[state.postsData.length - 1].id + 1,
                post: action.post,
                likesCount: Math.floor(Math.random() * 100)
            }
            return {
                ...state,
                postsData: [...state.postsData, newPost],
            }
        case DELETE_POST:
            return {
                ...state,
                postsData: state.postsData.filter(p => p.id !== action.postId)
            }

        case SET_USER_PROFILE:
            return {
                ...state,
                profile: action.profile
            }
        case SET_USER_STATUS:
            return {
                ...state,
                status: action.status
            }
        case SAVE_PHOTO_SUCCESS:
            return {
                ...state,
                profile: {...state.profile, photos: action.photos}
            }
        default:
            return state
    }
}
export const addPost = (post) => ({type: ADD_POST, post})
export const deletePost = (postId) => ({type: DELETE_POST, postId})


export const setUserProfile = (profile) => ({type: SET_USER_PROFILE, profile})
const setUserStatus = (status) => ({type: SET_USER_STATUS, status})
const savePhotoSuccess = (photos) => ({type: SAVE_PHOTO_SUCCESS, photos})
export const getUserProfile = (userId) => {
    return async (dispatch) => {
        const userData = await profileAPI.getProfile(userId)
        dispatch(setUserProfile(userData))
    }
}
export const getUserStatus = (userId) => {
    return async (dispatch) => {
        const userStatus = await profileAPI.getStatus(userId)
        dispatch(setUserStatus(userStatus))
    }
}
export const updateUserStatus = (status) => {
    return async (dispatch) => {
        const response = await profileAPI.updateStatus(status)
        if (response.data.resultCode === 0){
            dispatch(setUserStatus(status))
        }
    }
}

export const savePhoto = (photoFile) => {
    return async (dispatch) => {
        const response = await profileAPI.savePhoto(photoFile)
        if(response.data.resultCode === 0) {
            dispatch(savePhotoSuccess(response.data.data.photos))
        }
    }
}

export default profileReducer