import {
    getUsers, follow, unfollow
} from '../../Redux/usersReducer'
import { connect } from 'react-redux'
import * as React from 'react'
import Preloader from '../common/preloader/Preloader'
import Users from './Users'
import { withAuthRedirect } from '../../HOC/WithAuthRedirect'
import { compose } from 'redux'
import { getFromUsers } from '../../Redux/selectors/users-selectors'


class UsersContainer extends React.Component {
    componentDidMount() {
        this.props.getUsers(this.props.currentPage, this.props.pageSize)
    }

    onPageChanged = (pageNumber) => {
        this.props.getUsers(pageNumber, this.props.pageSize)
    }

    render() {
        return (
            <div>
                { this.props.isFetching ? <Preloader/> : null }
                <Users
                    totalUsersCount={ this.props.totalUsersCount }
                    pageSize={ this.props.pageSize }
                    currentPage={ this.props.currentPage }
                    onPageChanged={ this.onPageChanged }
                    users={ this.props.users }
                    follow={ this.props.followUser }
                    unfollow={ this.props.unfollowUser }
                    followingInProgress={ this.props.followingInProgress }
                />
            </div>

        )
    }
}

let mapStateToProps = (state) => {
    return {
        users: getFromUsers.users(state),
        pageSize: getFromUsers.pageSize(state),
        totalUsersCount: getFromUsers.totalUsersCount(state),
        currentPage: getFromUsers.currentPage(state),
        isFetching: getFromUsers.isFetching(state),
        followingInProgress: getFromUsers.followingInProgress(state),
    }
}
let mapDispatchToProps = (dispatch) => {
    return {
        followUser: (userId) => {
            dispatch(follow(userId))
        },
        unfollowUser: (userId) => {
            dispatch(unfollow(userId))
        },
        getUsers: (page, pageSize) => {
            dispatch(getUsers(page, pageSize))
        }
    }
}

export default compose(
    connect(mapStateToProps, mapDispatchToProps),
    withAuthRedirect
)(UsersContainer)
