import { useDispatch, useSelector } from 'react-redux'
import React from 'react'
import { loginUser } from '../../Redux/authReducer'
import { Redirect } from 'react-router-dom'
import { LoginForm } from '../common/Forms/LoginForm'
import classes from './Login.module.css'
import { getFromAuth } from '../../Redux/selectors/profile-selectors'


const Login = () => {
    const dispatch = useDispatch()
    const isAuth = useSelector(state => getFromAuth.isAuth(state))
    const logIn = (formData) => {
        dispatch(loginUser(formData))
    }
    if (isAuth){
        return <Redirect to='/profile'/>
    }
    return (

        <div className={ classes.loginForm }>
            <div><b>Данные тестового аккаунта:</b></div>
            <div>
                <p><b>Email:</b> <i>free@samuraijs.com</i></p>
                <p><b>Пароль:</b> <i>free</i></p>
            </div>
            <h1 style={ {textAlign: 'center'} }>Войдите</h1>
            <LoginForm onSubmit={ logIn }/>
        </div>
    )
}

export default Login