import myLogo from './../../img/Logo-Logo.svg'
import classes from './Header.module.css'
import { NavLink } from 'react-router-dom'

const Header = (props) => {

    return (
        <header className={ classes.header }>
            <div className={ classes.logoBlock }>
                Made with <img className={ classes.logo } src={ myLogo } alt="reactLogo"/>

            </div>
            <div className={ classes.loginBlock }>

                { props.user.isAuth
                    ? <div className={ classes.login }>Приветствую, { props.user.login }
                        <button className={ classes.button } onClick={ props.logout }>Выйти</button>
                    </div>
                    : <NavLink className={ classes.login } to={ '/login' }>Войти</NavLink>
                }

            </div>
        </header>
    )
}

export default Header