const ADD_MESSAGE = 'dialogs/ADD-MESSAGE'

let initialState = {
    dialogsData: [
        {id: 1, nickname: 'Ragnarek'},
        {id: 2, nickname: 'SomeNickname'},
        {id: 3, nickname: 'anotherNickName'},
        {id: 4, nickname: 'Solnyshko'},
        {id: 5, nickname: 'AntonAnton'},
        {id: 6, nickname: 'VasyaVasin'},
    ],

    messagesData: [{id: 1, message: 'Hi! How are you?'}],
}

const dialogsReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_MESSAGE:
            let newMessage = {
                id: state.messagesData[state.messagesData.length - 1].id + 1,
                message: action.message,
            }
            return {
                ...state,
                messagesData: [...state.messagesData, newMessage],
            }

        default:
            return state
    }
}
export const addMessage = (message) => ({type: ADD_MESSAGE, message})

export default dialogsReducer
